import fetchHelper from "../../../helpers/fakeFetchHelper";

export const getJoinRequests = async ({
  rowsPerPage = 10,
  page = 0,
  sort = "asc",
  apiCallStart,
  apiCallSuccess,
  apiCallFailure
}) => {
  const apiResponse = {
    success: true,
    users: [
      { name: "", stdNumber: "16101341", userId: "kangji1610" },
      { name: "", stdNumber: "15101261", userId: "sy1510" },
      { name: "", stdNumber: "18101826", userId: "dongha1810" },
      { name: "", stdNumber: "18101926", userId: "doh18" },
      { name: "", stdNumber: "15101729", userId: "gw1741" },
      { name: "", stdNumber: "16101274", userId: "kimkim715" },
      { name: "", stdNumber: "16101037", userId: "jjj391" },
      { name: "", stdNumber: "12101348", userId: "yjs1013" },
      { name: "", stdNumber: "14101242", userId: "ijh34879" }
    ],
    page,
    count: rowsPerPage,
    total: 9
  };

  await apiCallStart();

  await fetchHelper
    .get(
      `/admin/user?limit=${rowsPerPage}&page=${page}&sort=${sort}`,
      null,
      apiResponse
    )
    .then(response => apiCallSuccess(response))
    .catch(() => apiCallFailure());
};

export const postJoinRequestReview = async ({
  userId,
  joinRequestType,
  currentPage = 1,
  apiCallStart,
  apiCallSuccess,
  apiCallFailure
}) => {
  const apiResponse = { success: true };

  await apiCallStart();

  await fetchHelper
    .put(`/admin/user/${joinRequestType}/${userId}/`, { userId }, apiResponse)
    .then(() => apiCallSuccess(null, currentPage))
    .catch(() => apiCallFailure());
};
